@import "../../Styles/Sass/mixins.scss";
@import "../../Styles/Sass/Variables.scss";

.title {
  @include text(15px, 1.285714285714286em, 300, $textColor);
  margin: 30px;
  text-transform: uppercase;



  & h2 {
    @include h2($textThirdColor);

    & span {
      @include h2(blue);

    }
  }
}












